<template>
    <div>
        <table class="table table-hover table-dark rounded haven-table" v-if="showHavenReward">
            <thead>
                <tr>
                    <th scope="col" style="width: 25%;">
                        <span>Hero</span>
                    </th>
                    <th scope="col" style="width: 20%;">
                        <span>Drop_1</span>
                    </th>
                    <th scope="col">
                        <span>%</span>
                    </th>
                    <th scope="col" style="width: 20%;">
                        <span>Drop_2</span>
                    </th>
                    <th scope="col">
                        <span>%</span>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in tableData" :key="item">
                    <th scope="row">{{ item.displayName }}</th>
                    <td>
                        <img :src="`img/EquipmentItem/${item.dropItem1}`" 
                        :alt="item.dropItem1">
                    </td>
                    <td>{{ item.dropRate1 }}%</td>
                    <td>
                        <img :src="`img/EquipmentItem/${item.dropItem2}`" 
                        :alt="item.dropItem2">
                    </td>
                    <td>{{ item.dropRate2 }}%</td>
                </tr>
            </tbody>
        </table>
        <div class="d-flex justify-content-center" style="height:50vh;" v-else>
            <div class="spinner-border my-auto" role="status">
                <span class="visually-hidden">Loading...</span>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref } from 'vue'
import { useInitUnit } from '@/store/InitUnit'
import { storeToRefs } from 'pinia'

export default {
    name: 'HavenReward',
    setup() {
        const showHavenReward = ref(false)
        setTimeout(() => {
            showHavenReward.value = true
        }, "1000")

        useInitUnit().setHavenReward()
        const { allChars } = storeToRefs(useInitUnit())
        const tableData = ref(allChars.value.chars)
        
        console.log(tableData)
        return {
            tableData,
            showHavenReward
        }
    }
}
</script>

<style scoped>
table {
    table-layout:fixed;
    text-align: center;
}
img {
    width: 100%;
}
@media screen and (max-width: 768px) {
    .haven-table>thead>tr>th {
        font-size: xx-small;
    }
}
</style>
